import { Component, h } from '@stencil/core';

/**
 * With slot in the header
 * This example demonstrates how to use the header slot in a collapsible section.
+ * By using the slot="header" attribute, you can place custom UI elements in the
+ * header area of the collapsible section alongside the default header text.
+ * This enables richer header content like status indicators, badges, or icons.
 */
@Component({
    tag: 'limel-example-collapsible-section-with-slot-in-header',
    shadow: true,
})
export class CollapsibleSectionWithSlotInHeaderExample {
    public render() {
        return (
            <limel-collapsible-section header="This section has custom content in the header">
                <limel-circular-progress
                    slot="header"
                    value={65}
                    size="x-small"
                />
                <p>This element becomes the body.</p>
            </limel-collapsible-section>
        );
    }
}
